@import url('https://fonts.googleapis.com/css?family=Pacifico&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 20vw;
  height: 20vw;
}

.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.container {
  width:25vw;

  display: inline;

}
input{
  display: block;
  margin: auto;
  
  
}
.form-group input{
  margin-bottom: 1em;
  width: 30vw;
  border-radius:2px;
}
.form-group{
  border: 3px solid pink;
  border-radius:10px;
  padding: 1em;
  box-shadow: 10px 10px 5px pink;
}

.image {
  width: 20vw;
  height: 20vw;
  background-color: lightpink;
}

.form-control {
  width: 20%;
  margin: auto;
}

.header {

  background: url("http://www.bife.ie/_fileupload/Image/Banner_fashiontheatremediamakeup.jpg");
  background-size:cover;
  border: 2px solid pink;
  padding: 2em 0em;
  margin: 0 0 1em 0;
  font-family: 'Pacifico', cursive;
}

.header h1 {
  margin: 1em;
  font-size: 3em;
  color:white;
}

.go-to-cart {
  text-align: right;
  margin: 1em;

}

.cart-btn {
  margin: 1em;

}

.cart-img {
  width: 4vw;
  height: auto;

}
.checkout-items{
  display:flex;
  justify-content:space-evenly;
}

.price {
  display: flex;
  justify-content: space-between;
  align-content: center;
  margin-top: 1em;
}

.searchForm{
  text-align:center;
}
.card-title{
  margin: 1em;
}
